}

html {
  font-size: 20px;
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  background-color: #3f3f3f;
  color: #fff;
}

#root {
  display: grid;
  grid-template-columns: 1fr;
  place-items: center;
  width: 100%;
  height: 100vh;
}

#container {
